import React, { useState, useEffect } from 'react';
import { Text, View } from 'react-native';
import { ScrollView } from 'react-native-gesture-handler';
import { SafeAreaView } from 'react-native-safe-area-context';
import Icon from 'react-native-vector-icons/Ionicons';
import { useFocusEffect } from '@react-navigation/native';
import AsyncStorage from '@react-native-async-storage/async-storage';

import style from '../styles/storeStyle';

export default function StoreScreen() {
  const [coins, setCoins] = useState(0);
  const [click, setClick] = useState(1);
  const [clickCost, setClickCost] = useState(100);

  const storeData = async (key, value) => {
    try {
      await AsyncStorage.setItem('@'+key, value)
    } catch (e) {
      // saving error
    }
  }

  const getData = async (key) => {
    try {
      const value = await AsyncStorage.getItem('@'+key)
      if (value !== null) {
        if (key == 'upgradeClick') {
          setClick(value)
          setClickCost(value * 123 * value)
        }
      }
    } catch(e) {
      // error reading value
    }
  }

  function upgradeClick() {
    if (coins > clickCost || coins == clickCost) {
      const coinsRemoving = clickCost;
      fetch('https://antwanve.nl/globalclickergame/removeCoin.php?coins='+coinsRemoving+'&key=19ASD9381NF23D432FGDA')
        .then(response => response.json())
        .then(data => {
          if (data.status == 'success') {
            storeData('upgradeClick', parseInt(click) + 1);
            getData('upgradeClick')
            setCoins(coins - clickCost)
          } else if (data.status == 'failed') {
            alert('Not enough coins')
          } else {
            alert('An error occurred while talking to the server, please try again later')
          }
        })
      .catch(function (err) {
        // console.log(err);
        alert('An error occurred while talking to the server, please try again later')
      });
    } else {
      alert('Not enough coins')
    }
  }

  function loadGlobalCoins() {
    // console.log('Loading Coins...')
    fetch('https://antwanve.nl/globalclickergame/getCoins.php?key=19ASD9381NF23D432FGDA')
      .then(response => response.json())
      .then(data => {
        // console.log('Loading Coins Success')
        setCoins(parseInt(data.coins) + window.globalCoinsToUpload);
      })
    .catch(function (err) {
      console.log(err);
    });
  }

  useFocusEffect(() => {
    setCoins(parseInt(window.globalcoins) + window.globalCoinsToUpload)
  }, [])

  useEffect(() => {
    getData('upgradeClick');
    setInterval(() => {
      loadGlobalCoins()
    }, 10000);
  }, []);

  return (
    <ScrollView style={style.page}>
      <SafeAreaView>

        <View style={style.titleBar}>
          <Text style={style.title}>Store</Text>
          <View style={style.coinTotal}>
            <Text style={style.coinTotalText}>{coins}</Text>
            <Icon name={'ios-disc'} size={18} color={'yellow'} />
          </View>
        </View>

        <Text style={style.sectionTitle}>Personal</Text>
        <View style={style.upgrade}>
          <Text style={style.upgradeText}>+1 per click</Text>
          <View style={style.upgradeButtons} onStartShouldSetResponder={ () => upgradeClick() }>
            <View style={style.upgradeButtonPrice}>
              <Text style={style.upgradeButtonPriceText}>{clickCost}</Text>
              <Icon name={'ios-disc'} size={18} color={'yellow'} />
            </View>
            <View style={style.upgradeButtonIcon}>
              <Icon name={'ios-arrow-up'} size={30} color={'white'} />
            </View>
          </View>
        </View>

        <Text style={style.sectionTitle}>Global</Text>

      </SafeAreaView>
    </ScrollView>
  );
}