import React, { useState } from 'react';
import { View, Dimensions, Text } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import Icon from 'react-native-vector-icons/Ionicons';

const Tab = createBottomTabNavigator();

//* IMPORT PAGES
import ClickyScreen from './pages/clickyPage';
import StoreScreen from './pages/storePage';

//* MAIN APP
export default function App() {
  const [navWidth, setNavWidth] = useState(Math.round(Dimensions.get('window').width));

  Dimensions.addEventListener('change', () => {
    setNavWidth(Math.round(Dimensions.get('window').width))
  });

  return (
    <NavigationContainer>
      <Tab.Navigator
        screenOptions={({ route }) => ({
          tabBarIcon: ({ focused, color, size }) => {
            let iconName;
            let iconActive;
            if (route.name === 'Clicky') {
              iconName = focused
                ? 'ios-flash'
                : 'ios-flash';
              iconActive = focused
                ? 0.8
                : 0;
            } else if (route.name === 'Store') {
              iconName = focused
                ? 'ios-cart'
                : 'ios-cart';
              iconActive = focused
                ? 0.8
                : 0;
            }
            return <View style={{ flex: 1, flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                    <Icon name={iconName} size={size} color={color} style={{ zIndex: 99 }} />
                    <View style={{ position: 'absolute', shadowColor: '#FFF', shadowOpacity: iconActive, shadowRadius: 20, elevation: 10, color: '#1B1C22' }}><Text>â€¢</Text></View>
                  </View>;
          },
        })}
        tabBarOptions={{
          activeTintColor: '#FFFFFF',
          inactiveTintColor: '#707070',
          showLabel: false,
          style: {
            backgroundColor: '#1B1C22',
            borderTopWidth: 0,
            minHeight: 75,
            borderRadius: 50,
            position: 'absolute',
            bottom: 30,
            right: navWidth / 2 - 100,
            left: navWidth / 2 - 100,
            maxWidth: 500,
            paddingBottom: 0,
            shadowColor: "#000",
            shadowOffset: {
              width: 0,
              height: 5,
            },
            shadowOpacity: 0.2,
            shadowRadius: 15,
            elevation: 10,
          }
        }}
      >
        <Tab.Screen name="Clicky" component={ClickyScreen} />
        <Tab.Screen name="Store" component={StoreScreen} />
      </Tab.Navigator>
    </NavigationContainer>
  );
}